*, *::before, *::after{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root {
    --primary-hue: 210;
    --gray-hue: 240;
    --color-primary: hsl(var(--primary-hue), 100%, 50%);
    --color-primary-variant: hsl(var(var(--primary-hue)), 100%, 65%);

    --color-secondary: #feecc7;
    --color-gray-100: hsl(var(--gray-hue), 47%, 94%);
    --color-gray-200: hsl(var(--gray-hue), 26%, 75%);
    --color-gray-300: hsl(var(--gray-hue), 24%, 64%);
    --color-gray-400: hsl(var(--gray-hue), 16%, 41%);
    --color-gray-500: hsl(var(--gray-hue), 44%, 25%);
    --color-gray-600: hsl(var(--gray-hue), 48%, 15%);
    --white-color: #FFFFFF;

    --container-width-lg: 80%;
    --cotainer-width-md: 90%;

    --trasition: all 500ms ease;
}


body {
    font-family: 'Montserrat', sans-serif;
    color: var(--color-gray-100);
    line-height: 1.7;
    overflow-x: hidden;
    background: var(--color-gray-600);
}

.container {
    width: var(--container-width-lg);
    max-width: 1920px;
    margin-inline: auto;
}

h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
    color: var(--color-gray-200);
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

a {
    color: var(--color-gray-600);
}

img {
    display: block;
    object-fit: cover;
    width: 100%;
}


.btn {
    color: var(--color-gray-400);
    width: fit-content;
    margin: 0 auto;
    padding: 0.9rem 2rem;
    background-color: var(--white-color);
    border-radius: 1.5rem;
    transition: var(--trasition);
}

.btn:hover{
    background: var(--color-gray-400);
    color: #FFFFFF;
    
}

.btn.lg {
    padding: 1.2rem 3rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}

.btn.sm {
    padding: 0.4rem 1.2rem;
    font-size: 0.9rem;
}

.btn.sm:hover{
    background: var(--white-color);
}



section {
    margin-top: 10rem;

}

.section__head {
    display: flex;
    align-items: center;
    gap: 1rem;


}

.section__head span {
    background: var(--color-gray-500);
    padding: 0.8rem;
    border-radius: 1rem;
    color: var(--color-secondary);
    font-size: 1.5rem;
}

.card {
    background: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 10px;
    transition: var(--trasition);
}

.card:hover{
    background: transparent;
    border-color: var(--color-gray-400);
    cursor: default;
}

.card span{
    width: 3rem;
    height: 3rem;
    background: var(--white-color);
    color: hsl(230, 48%, 24%);
    font-size: 1.5rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: var(--trasition);
}

.card:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}

.card small {
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}


.header {
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid var(--color-gray-400);
}

.header__container {
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}

.header__container-bg{
    position: absolute;
    width: 100%;
}

.header__container-bg img{
    opacity: 0.4;
}

.header__content{
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: var(--white-color)
}

.header__content h2{
    margin-bottom: 1rem;
    color: var(--white-color)
}

.header__content p{
    color: var(--color-gray-100);
}


/* Media queries */

@media screen and (max-width:1024px){
    .container {
        width: var(--cotainer-width-md);

    }

    h1 {
        font-size: 2rem;
    }

    h2 {
        font-size: 1.6rem;
    }

    .header__content{
        width: 76%;
    }
}

@media screen and (max-width:600px){

    h1 {
        font-size: 2.2rem;
        line-height: 1.3;
    }
    section {
        margin-top: 7rem;
    }

    .header {
        height: fit-content;
    }

    .header__content{
        width: var(--cotainer-width-md);
        padding: 3rem 0;
    }

    .header__content p{
        font-size: 0.85;
    }

}
